@font-face {
  font-family: DelugeCashFont;
  src: url(./assets/font/TildaSans-VF.woff2);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: #fff;
  font-family: "DelugeCashFont";
}

body {
  background-color: #0b1217;
}

.container {
  display: flex;
  flex-direction: column;
  position: relative;
  margin: 0px auto;
  width: 100%;
  -webkit-box-flex: 1;
}

.ityped-cursor {
  font-size: 2.8rem;
  color: yellow;
  opacity: 1;
  -webkit-animation: blink 0.3s infinite;
  -moz-animation: blink 0.3s infinite;
  animation: blink 0.3s infinite;
  animation-direction: alternate;
}

.App {
  position: relative;
  overflow-x: hidden;
  min-height: 100vh;
}

.MuiInputBase-root {
  color: white !important;
}

@keyframes blink {
  100% {
    opacity: 0;
  }
}

@-webkit-keyframes blink {
  100% {
    opacity: 0;
  }
}

@-moz-keyframes blink {
  100% {
    opacity: 0;
  }
}

@media (max-width: 576px) {
  .container {
    width: calc(100% - 15px);
  }
}

@media (min-width: 576px) {
  .container {
    width: 540px;
  }
}

@media (min-width: 768px) {
  .container {
    width: 720px;
  }
}

@media (min-width: 992px) {
  .container {
    width: 960px;
  }
}

@media (min-width: 1200px) {
  .container {
    width: 1140px;
  }
}

@media (min-width: 1400px) {
  .container {
    width: 1320px;
  }
}
